import {Injectable} from '@angular/core';
import {ToastController,LoadingController,Events } from 'ionic-angular';
import {BaseUI} from "../../common/baseui";
// import {LoginPage} from '../../pages/login/login'
@Injectable()
export class HttpInterceptHandle extends BaseUI {
  constructor(public events: Events, public toastCtrl: ToastController,public loadingCtrl: LoadingController) {
      super();
    events.subscribe('request:before', (url, options) => {
      console.log('loading..')
     
      if((url).indexOf('needload') >-1) { //
        super.showLoading(this.loadingCtrl,'');
      }
      console.log('%c  %c', 'color:blue', '', 'url', url, 'options', options);
    });

    events.subscribe('request:success', (url, options, res) => {
      if((url).indexOf('needload') <0) { //loading
        super.hideLoading(this.loadingCtrl);
      }
      console.log('%c  %c', 'color:green', '', 'url', url, 'options', options, 'res', res);
    });

    events.subscribe('request:error', (url, options, error) => {
      console.log('%c  %c', 'color:red', '', 'url', url, 'options', options, 'error', error);
      let status = error.status;
      if (status === 0) {
        // nativeService.showToast('');
        super.showToast(this.toastCtrl, '',1000);
      } else if (status === 404) {
        // nativeService.showToast('');
        super.showToast(this.toastCtrl, '',1000);
      } else if (status === 500) {
        // nativeService.showToast('');
        super.showToast(this.toastCtrl, '',1000);
        
      } else {
        // nativeService.showToast('');
        super.showToast(this.toastCtrl, '',1000);
        
      }
    });
  }

}




