import {Injectable} from '@angular/core';
import {Platform, AlertController} from 'ionic-angular';
import {AppVersion} from '@ionic-native/app-version';
import {File} from '@ionic-native/file';
import {Transfer, TransferObject} from '@ionic-native/transfer';
import {FileOpener} from '@ionic-native/file-opener';
import {InAppBrowser} from '@ionic-native/in-app-browser';
// import {APP_DOWNLOAD, APK_DOWNLOAD} from "./Constants";

@Injectable()


export class NativeService {
   public APP_DOWNLOAD = 'http://wechat.chepass.com:8021/Content/dist/';//ios app
   public APK_DOWNLOAD = 'http://wechat.chepass.com:8021/Content/dist/android-release.apk';//apk
  constructor(private platform: Platform,
              private alertCtrl: AlertController,
              private transfer: Transfer,
              private appVersion: AppVersion,
              private file: File,
              private fileOpener: FileOpener,
              private inAppBrowser: InAppBrowser) {
  }


  /**
   * app
   */
  detectionUpgrade() {
    //app,app(this.getVersionNumber())
    //,return
    this.alertCtrl.create({
      title: '',
      subTitle: ',',
      buttons: [{text: ''},
        {
          text: '',
          handler: () => {
            this.downloadApp();
          }
        }
      ]
    }).present();
  }

  /**
   * app
   */
  downloadApp() {
    if (this.isAndroid()) {
      let alert = this.alertCtrl.create({
        title: '0%',
        enableBackdropDismiss: false,
        buttons: ['']
      });
      alert.present();

      const fileTransfer: TransferObject = this.transfer.create();
      const apk = this.file.externalRootDirectory + 'android.apk'; //apk
      console.log('apk--externalRootDirectory-', this.file.externalRootDirectory)
      fileTransfer.download(this.APK_DOWNLOAD, apk).then(() => {
          console.log('this.APK_DOWNLOAD---', this.APK_DOWNLOAD)
       this.fileOpener.open(apk,'application/vnd.android.package-archive');
      });

      fileTransfer.onProgress((event: ProgressEvent) => {
        let num = Math.floor(event.loaded / event.total * 100);
        if (num === 100) {
          alert.dismiss();
        } else {
          let title = document.getElementsByClassName('alert-title')[0];
          // console.log('num---', num)
          title && (title.innerHTML = '' + num + '%');
        }
      });
    }
    if (this.isIos()) {
      this.openUrlByBrowser(this.APP_DOWNLOAD);
    }
  }

  /**
   * url
   */
  openUrlByBrowser(url:string):void {
    this.inAppBrowser.create(url, '_system');
  }

  /**
   * 
   * @return {boolean}
   */
  isMobile(): boolean {
    return this.platform.is('mobile') && !this.platform.is('mobileweb');
  }

  /**
   * android
   * @return {boolean}
   */
  isAndroid(): boolean {
    return this.isMobile() && this.platform.is('android');
  }

  /**
   * ios
   * @return {boolean}
   */
  isIos(): boolean {
    return this.isMobile() && (this.platform.is('ios') || this.platform.is('ipad') || this.platform.is('iphone'));
  }

  /**
   * app,0.01
   * @description  /config.xmlversion
   * @returns {Promise<string>}
   */
  getVersionNumber(): Promise<string> {
    return new Promise((resolve) => {
      this.appVersion.getVersionNumber().then((value: string) => {
        resolve(value);
      }).catch(err => {
        console.log('getVersionNumber:' + err);
      });
    });
  }
}



